// Grid breakpoints
$grid-breakpoints: (
  xs: 0,
  sm: 768px,
  md: 992px,
  lg: 1200px
) !default;

$grid-breakpoint-xs: 480px;
$grid-breakpoint-sm: 768px;
$grid-breakpoint-md: 992px;
$grid-breakpoint-lg: 1200px;
$grid-breakpoint-mobile-nav: 1200px;

$spacer: 1rem !default;
$spacers: () !default;
$spacers: map-merge((
  0: 0,
  1: ($spacer * .25),
  2: ($spacer * .5),
  3: $spacer,
  4: ($spacer * 1.5),
  5: ($spacer * 3),
  6: ($spacer * 4.5),
  7: ($spacer * 5),
  8: ($spacer * 5.5),
  9: ($spacer * 6)
), $spacers);


// Colors
$color-primary: #7254c9;
$color: #27282a;
$color-secondary: darken($color-primary, 25%);
$color-primary: #7254c9;
$color-gradient-start: $color-primary;
$color-gradient-stop: darken(#7254c9, 50%);
$white: #f2f2f2;
$muted-purple: #7D5BA6;
$blue: #7D5BA6;
$off-gray: #A2A3BB;
$black: #272B2C;
$bg-color: #27282a;
$bg-vulcan: #141622;
$bg-vulcan2: #3b3b3b;

:root {
    --color-primary: $color-primary;
    --color-secondary: darken($color-primary, 25%);
    --color-gradient-start: $color-primary;
    --color-gradient-stop: darken($color-gradient-start, 50%);
    --white: $white;
    --muted-purple: $muted-purple;
    --blue: $blue;
    --off-gray: $off-gray;
    --black: $black;
}

